///////////////////////////////////////////////////////////////////////
// resets
// TODO: clean styles
///////////////////////////////////////////////////////////////////////
ul, li, table, form, p, h1, h2, h3 {
  margin: 0;
  padding: 0;
  text-indent: 0; }

ul {
  list-style-type: none;
  &.text {
    margin: 8px 0 10px 0 !important;
    padding: 0 !important;
    li {
      margin: 0 !important;
      padding: 0 180px 0 19px; } } }





.table {
  width: 100%;

  thead {
    th {
      color: #999;
      font-weight: normal;
      text-align: left; } }

  &--default {
    margin: 14px 0;
    border-collapse: separate;
    border-spacing: 0 5px; }



  //tbody
  //  tr
 }  //    border-bottom: 1px solid red


img {
  border: none; }

input, textarea {
  margin: 0;
  padding: 0; }

.hidden {
  display: none; }


// TODO: implement propperly
#heading {
  display: none; }


