// TODO: refactor
.listview.container {
  .play-all-wrapper {
    text-align: center;

    margin: 20px 0 12px;
    .wrapper {
      padding: 10px;
      background: $body-color-shaded-bg;
      //font-style: italic
      position: relative;
      text-align: center;

      &:after {
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 0;
        height: 0;
        border-top: solid 9px #f9f9f9;
        border-left: solid 9px transparent;
        border-right: solid 9px transparent; }

      .play-button {
        //background: black
        width: 120px;
        margin: 0 auto;
        padding: 3px 8px 2px;
        text-transform: uppercase;
        font-size: 16px;
        color: #a5a5a5;
        cursor: pointer;

        &.hover {
          color: $primary-color-a; } } } } }




.listview {
  &.container {


    .list_body_row {
      ul {
        margin: 0; }

      .license { // TODO: move to better place
        img {
          height: 10px; } } }


    /*background-color: #ababab; */
    display: table;
    width: 100%;
    border-spacing: 0px;
    // width: 730px
    .list_header {
      display: table-row;
      //background-color: #E5E5E5
      color: #666666;
      //text-transform: uppercase
      font-style: italic;
      font-size: 12px !important;
      .list_header_item {
        display: table-cell;
        //border-bottom: 1px solid #BBBBBB
        &.sortable {
          //background-color: #dddddd
          background-position: right center;
          background-repeat: no-repeat;
 }          // background-image: url('/static/img/prototype/table_order_indicator.png')
        &.active {
          background-color: #CCCCCC;
          background-color: #fafafa;
          background-position: right center;
          background-repeat: no-repeat;
