.listview {
  &.container {

    .list_body {
      display: table-row-group;
      .list_body_row {
        display: table-row;

        background: $body-color-shaded-bg;
        transition-property: background;
        transition-duration: 0.3s;

        // inline smalls (eg country)
        a > small {
          color: #5a5a5a;
          font-weight: normal; }

        /* Status dependant styleing */
        &.selection {
          //background-color: #E4FFE4
          background-color: $primary-color-b-bg-light;
          li.indicator {
            // background-image: url(/static/img/base/listview.row.selection.bg.png)
            background-repeat: no-repeat;
            background-position: 0 -16px; } }

        &.selection.hover {
          background-color: #cbe5cb; }


        &.locked {
          // background-image: url(/static/img/prototype/list_closed_bg.png)
          background-color: #ffe1d2; }
        &.hover {
          background-color: $hover-bg-color; }

        /* Unpublished, grey text */
        &.unpublished {
          .list_body_item a {
            color: #666666; } }
        /* Owner */
        //&.owner
 }        //  background-image: url(/static/img/prototype/list_closed_bg.png)






      .list_body_item {
        vertical-align: top !important;
        display: table-cell;
        border-bottom: 4px solid $body-color-bg;
        color: #666666;
        .small {
          line-height: 18px; }

        ul.small {
          li {
            font-weight: normal;
            font-size: 90%;
            &.highlight {
              text-transform: uppercase;
              small {
                color: $primary-color-a; } } } } } } } }



