    padding: 2px 5px; }

  .list_body_row.item {
    background-size: 197px 197px; }


  div.list_body {
    display: block;

    div.break_line {
      height: 55px; }

    /* actually not a real 'row' in this style */
    div.list_body_row {
      display: block;
      float: left;
      width: 197px;
      height: 277px;
      margin: 14px 11px 0 0;
      &:nth-child(4n) {
        margin: 14px 0 0 0; }

      background-repeat: no-repeat;
