      border: 1px solid #EFEFEF;
      position: relative;

      &.selection {
        div.wrapper_info, div.wrapper_action {
          background-color: $selection-bg-color; } }

      &.hover {
        border-bottom: none;
        //background-color: #ffffff
        .wrapper_info {
 }          //background-color: #F5F5F5
        .wrapper_action {
 } }          //background-color: #F5F5F5
      &.selection.hover {
        background-color: $selection-bg-color; }

      &.playing {
        ul li {
          &.play a {
            background-position: 0 -16px; } } }


      //&.pos3
      //  margin: 9px 0 0 0

      div.list_body_item {
        display: block;
        border-bottom: none;

        &.action {
          .wrapper {
            width: 36px;
            padding: 0 0 0 150px; } } }



      /* Alternative Play icon */
      .wrapper_icon {
        position: absolute;
        left: 51px;
        top: 73px;
        padding: 4px;
