        //height: 195px
        height: 197px;


        // height: 82px // simulate hover state
        -webkit-transition-property: height;
        -webkit-transition-duration: .1s;
        // background-color: #ff00ff
        &.license {
          background-position: right bottom; }

        // cloud (inline)
        ul.tags {
          position: absolute;
          bottom: 0px;
          right: 0;
          opacity: 0.88;
          padding-bottom: 2px;
          li {
            margin: 2px 2px 0 0;
            padding: 0 4px;
            line-height: 14px;
            background: #333;
            color: white;
            border: 1px solid #343434;
            float: right;
            text-transform: uppercase;
            &.active {
              background: $primary-color-a;
              border-color: $primary-color-a; } } } }




      // animate on item hover

      //
      &.hover {
        ul.tags {
          position: absolute;
          opacity: 1;
          background-color: #333;
          -webkit-transition-property: opacity, background-color;
          -webkit-transition-duration: .1s;
          li {
            border: 1px solid #626262; } } }



      /* Adjustable info panel */
      .wrapper_info {
        //background-color: #ffffff
        ul li {
          /*padding: 2px 2px 2px 5px */ }
        .wrapper_inner {
          //height: 98px
          height: 60px;
          overflow: hidden; } }



      .wrapper_action {
        //background: #ffffff
        li {
          float: right;

          //
 } } }          //  font-size: 120%

    div.marker {
      position: absolute;
      top: 10px;
      right: -30px;
      span {
        text-transform: uppercase;
        background: #fff;
        display: block;
        text-align: center;
        -webkit-box-shadow: 2px 2px 4px 1px rgba(51, 51, 51, 0.35);
        -moz-box-shadow: 2px 2px 4px 1px rgba(51, 51, 51, 0.35);
        box-shadow: 2px 2px 4px 1px rgba(51, 51, 51, 0.35);
        width: 100px;
        padding: 2px 0;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        transform: rotate(45deg);
        a {
          color: #000; } }

      &.promo span {
        background: #fff; } } } }








////////////////////////////////////////////////////////////////////////////////////////////////////
// table based separator
////////////////////////////////////////////////////////////////////////////////////////////////////
.listview.container.actions,
.listview.container.import {
  table {
    tr:first-child > td {
      border-top: none; }
    tr.day-separator > td {
      padding: 10px 2px 4px;
      // text-align: center
      text-transform: capitalize;
      color: #888888;
      border-bottom: 1px solid #dedede;
      // font-style: italic
      background: none; } } }


/* preflight check */
.listview.container {
  .list_body_item {
    .preflight-check {
      //&.preflight-check-ok
      //  color: $primary-color-a
      &.preflight-check-error {
        color: $errorText; } } } }


/* license icon display */
.listview.container {
  .list_body_item {


    &.links.external {
      ul li {
        float: left;
        a {
          font-size: 16px;
          color: #5a5a5a;
          &:hover {
            color: $primary-color-b;
            text-decoration: none; } }
 } } }        // @include link_external


  &.s {
    .list_body_item {
      &.license {
        background-position: center center;
        background-position-y: 5px; } } }

  &.m {
    .list_body_item {
      &.license {
        background-position: 0 4px; } } } }


/* overrides for states */
.listview {
  &.container.downloads {
    .c1 {
      background-repeat: no-repeat;
      background-position: 2px 2px;
      width: 20px;
      img {
        width: 16px;
        height: 16px; } }

    .list_body_row.init {
      .list_body_item {
        color: #ababab; } }

    .list_body_row.running {
      .list_body_item {
        color: #ababab; } }

    .list_body_row.done {}

    .list_body_row.error {
      .list_body_item {
        background-color: #fff0e1;
        color: #5a5a5a; } }

    .list_body_row.done.downloaded {
      .list_body_item {
        background-color: #E4FFE4; } } } }



/* highlighter */
.listview.container .list_body {
  span.highlight {
    background-color: $primary-color-a-bg-light;
    color: $primary-color-a; } }




/* ---------- tagbar/search for gianluca :) ---------- */
.searchbar {
  padding: 2px 0 5px 0;
  background-color: $body-color-shaded-bg;
  border: 1px solid $body-color-shaded-bg;
  //margin-top: 8px
  margin-top: 0;
  z-index: 1002;
  transition-property: border;
  transition-duration: .3s;

  &.hover {
    // background-color: #333 !important
    //background-color: #efefef
    border: 1px solid $primary-color-b; }


  // search field (wrapper)
  .search-input {
    margin: 3px 0 0 2px;
    height: 17px;
    padding: 0;
    ::-webkit-input-placeholder {
      color: #777; }


    span, i {
      display: none; }
    input {
      border: 0px none;
      box-shadow: none !important;
      background: none;
      margin: 0px;
      height: 17px;
      line-height: 17px;
      padding: 0px 2px 0px 4px;
      &.blur {
        color: #5a5a5a; }
      &.focus {
        color: #333; } }
    a #searchbar_input {
      margin: 0 0 0 0;
      &.blur {
        color: $primary-color-a; }
      &.focus {
        color: #333333; } }


    &.active {
      .inner {
        background: $primary-color-a-bg-light;
        border: 1px solid white;
        border-right-color: #7ab89b;
        border-bottom-color: #7ab89b;
        input {
          color: $primary-color-a; }
        i {
          display: inline; }
        span {
          color: $primary-color-a;
          padding: 0 0 0 5px;
          display: inline; } } } }

  .search-clear {
    a {
      color: #5a5a5a;
      &:hover {
        color: $primary-color-a;
        text-decoration: none; } } }

  .filter-bar {
    float: right;
    margin: 0;
    a {
      color: $primary-color-a;
      background: $primary-color-a-bg-light;
      border: 1px solid white;
      border-right-color: #7ab89b;
      border-bottom-color: #7ab89b;
      float: right;
      padding: 0px 6px 0px 5px !important;
      margin: 3px 2px 0px 1px !important;
      &:hover {
        text-decoration: none; } } }

  // hacking icon
  i._icon.icon-delete {
    // background-image: url(/static/img/prototype/tag_remove.png)
    background-position: 0 11px;
    white-space: nowrap;
    padding: 0 0 0 12px; }

  // override defaults. TODO: remove original styles
  .tagbar {
    background: none;
    border: none;

    a {
      float: right;
      padding: 0px 6px 0px 5px !important;
      margin: 3px 2px 0px 1px !important; } } }
