// global styling for popup (the 'things' outside Vue app)

body.player {
  height: 100vh;
  min-height: 100vh;
  width: 100vw;
  //min-height: calc(100vh - 1px)
  //height: calc(100vh - 1px)
  background: #fafafa;
  overflow-x: hidden;
  overflow-y: hidden; }

.player-app {

  &.is-slave {
    background: rgba(110, 255, 228, 0.51);
    position: fixed;
    bottom: 300px;
    width: 100%; }

  .debug-container {
    background: rgba(255, 255, 255, 0.81);
    border: 2px solid red;
    position: fixed;
    bottom: 20px;
    left: 20px;
    width: 200px;
    height: 160px; } }
