@charset "UTF-8";


// listview
.listview.import {
  .items {
    margin-top: 14px; }

  .item {
    p.notes {
      font-style: italic; }

    .stats {
      @include clearfix;
      li {
        padding-right: 10px;
        &.completed span {
          color: $textColor;
          .done {
            color: $primary-color-a; } }
        &.duplicates span {
 }          //color: #00aeff
        &.warnings span,
        &.errors span {
          color: $errorText; } } } } }


// detail/edit view
.importer-importfiles {

  thead th {
    background-color: white;
    // text-align: center
    font-weight: normal;
    font-style: italic;
    color: #999;
    border: none; }


  i.icon-large {
    padding: 3px 0 0 10px;
    display: block;
    background-repeat: no-repeat;
