    //background-image: url(/static/img/icons/metadata.providers/id3v2.png) !important
    background-position: 0 0;
    width: 22px;
    height: 22px; }
  i.icon-musicbrainz_ {
    //background-image: url(/static/img/icons/metadata.providers/musicbrainz.png) !important
    background-position: 0 0;
    width: 22px;
    height: 22px; }
