// BUTTON GROUPS
// -------------

// Make the div behave like a button
.btn-group {
  position: relative;
  @include clearfix;
  // clears the floated buttons
  @include ie7-restore-left-whitespace; }

// Space out series of button groups
.btn-group + .btn-group {
  margin-left: 5px; }

// Optional: Group multiple button groups together for a toolbar
.btn-toolbar {
  margin-top: $baseLineHeight / 2;
  margin-bottom: $baseLineHeight / 2;
  .btn-group {
    display: inline-block;
    @include ie7-inline-block; } }

// Float them, remove border radius, then re-add to first and last elements
.btn-group .btn {
  position: relative;
  //float: left
  margin-left: -1px;
  @include border-radius(0);
  &[data-toggle="dropdown"] {
    float: left; } }

// Set corners individual because sometimes a single button can be in a .btn-group and we need :first-child and :last-child to both match
.btn-group .btn:first-child {
  margin-left: 0; }



// Reset corners for large buttons
.btn-group .btn.large:first-child {
  margin-left: 0; }


// On hover/focus/active, bring the proper btn to front

.btn-group .btn:hover,
.btn-group .btn:focus,
.btn-group .btn:active,
.btn-group .btn.active {
  z-index: 2; }

// On active and open, don't show outline

.btn-group .dropdown-toggle:active,
.btn-group.open .dropdown-toggle {
  outline: 0; }

// Split button dropdowns
// ----------------------

// Give the line between buttons some depth
.btn-group .dropdown-toggle {
  padding-left: 8px;
  padding-right: 8px;
  $shadow: inset 1px 0 0 rgba(255, 255, 255, 0.125), inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
  @include box-shadow($shadow);
  *padding-top: 5px;
  *padding-bottom: 5px; }

.btn-group.open {
  // IE7's z-index only goes to the nearest positioned ancestor, which would
  // make the menu appear below buttons that appeared later on the page
  *z-index: $zindexDropdown;
  // Reposition menu on open and round all corners
  .dropdown-menu {
    display: block;
    margin-top: 1px; }
  .dropdown-toggle {
    background-image: none;
    //$shadow: inset 0 1px 6px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05)
 } }    //+box-shadow($shadow)

// Reposition the caret
.btn .caret {
  margin-top: 7px;
  margin-left: 0; }

.btn:hover .caret,
.open.btn-group .caret {
  @include opacity(100); }

.btn-primary {
  .caret {
    border-top-color: $primary-color-b;
    opacity: 1.0; }
  &:hover .caret {
    border-top-color: white; } }

// Account for other colors
.btn-danger,
.btn-info,
.btn-success,
.btn-inverse {
  .caret {
    border-top-color: $white;
    @include opacity(75); } }

// Small button dropdowns
.btn-small .caret {
  margin-top: 4px; }
