// ALERT STYLES
// ------------

// Base alert styles
.alert {
  padding: 8px 35px 8px 14px;
  margin-bottom: $baseLineHeight;
  background-color: $warningBackground;
  color: #000;
  p {
    color: #000; } }

.alert,
.alert-heading {
  color: #000; }

// Adjust close link position
.alert .close {
  position: relative;
  top: -2px;
  right: -21px;
  line-height: 18px; }

// Alternate styles
// ----------------

.alert-success {
  background-color: $successBackground;
  border-color: $successBorder; }

.alert-success,
.alert-success .alert-heading {
  color: $successText; }

.alert-danger,
.alert-error {
  background-color: $errorBackground;
  border-color: $errorBorder; }

.alert-danger,
.alert-error,
.alert-danger .alert-heading,
.alert-error .alert-heading {
  color: $errorText; }

.alert-info {
  background-color: $success-color-bg; }



// Block alerts
// ------------------------
.alert-block {
  padding-top: 14px;
  padding-bottom: 14px; }

.alert-block > p,
.alert-block > ul {
  margin-bottom: 0; }

.alert-block p + p {
  margin-top: 5px; }
