@charset "UTF-8";

@mixin input-container {
  margin: 0 0 8px 0;
  display: flex;
  flex-direction: column;



  &__field {
    display: flex;
    width: 100%;
    align-items: center;

    label {
      margin-right: 12px;
      font-weight: 300; }

    input {
      padding: 4px;
      transition-property: none; }

    .fl-wrap-input {
      width: 100%; } }

  &__field-help {
    display: none;
    width: 100%;
    font-size: 80%;
    opacity: 0.8; }

  &__field-errors {
    width: 100%;
    //font-size: 80%
    //font-weight: 400
    color: $pink;
    padding-top: 4px; }

  &--has-errors {
    input {
      border-color: $pink; } } }


