form select, form file {

  border-radius: 0;
  box-shadow: none;
  background: white;
  border: none;
  border-bottom: 1px dotted #a9a9a9;
  //outline: 1px solid #ccc
  font-size: inherit;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #555;
  margin: 0;
  line-height: 17px !important;
  padding: 0; }



// autocomplete
form input {
  &.autocompletewidget {
    background-image: url(/static/img/css/autocomplete.png) !important;
    background-position: center right !important;
    background-repeat: no-repeat !important; } }



form {
  input:focus,
  select:focus,
  textarea:focus {
    background: $primary-color-b-bg-light;
    border-color: $primary-color-b-bg-light;
    border-style: solid; } }

// temporary: forced minimalistic style
input,
textarea,
select,
.uneditable-input {
  color: $gray;
  //border: 1px solid #F5F5F5
  //border-bottom: 1px dotted #a9a9a9
  border: 1px solid #dcdcdc; }


// HELP TEXT
// ---------

.help-block {
  display: inline;
  padding-left: 6px;
  margin-top: 5px;
  margin-bottom: 0;
  color: #999; }



.help-inline {
  display: inline-block;
  margin-bottom: 9px;
  vertical-align: middle;
  padding-left: 5px; }

.ui-helper-hidden-accessible {
  display: none; }



// Horizontal-specific styles / Overrides
// --------------------------

form.form-horizontal {
  // Increase spacing between groups
  .control-group {
    margin-bottom: $baseLineHeight / 2; }
  // Float the labels left
  .control-label {
    float: left;
    width: auto;
    padding-top: 2px;
    text-align: left;
    margin-bottom: 2px; }
  // Move over all input controls and content
  .controls {
    margin-left: 120px;
    select {
      height: 20px; }
    input {
      height: 24px;
      padding: 1px 0 0 4px;
      line-height: 11px; } }
  // Move over buttons in .form-actions to align with .controls
  .form-actions {
    padding-left: 160px;
    border-top: none; }

  .span2 {
    .controls {
      margin-left: 50px; }
    &.unpadded {
      padding-left: 0px;
      margin-left: 0px; } }



  // sorry, hacks here...
  > .form-row,
  #release_media_form > .form-row {
    margin-bottom: 9px;
    margin-top: 9px;
    padding-top: 9px;
    margin-left: 5px;
    .controls {
      margin-left: 70px !important; }

    &:nth-child(odd) {
      background: #fafafa; }

    &.relation-row {
      padding: 0 0 0 0;
      margin-top: 0; }

    &.releasemedia-row {
      margin-left: 0;
      padding: 8px 0 4px 0; }

    .span3 {
      select {
        background: #FAFAFA;
        border: none;
        outline: 1px solid #E4E4E4; } }

    .span9 {
      input {
        width: 206px; } } }


  .relation-row,
  .albumartist-row,
  .extraartist-row,
  .mediaartist-row {
    .span3 select {
      width: 80px; }
    .span5 .controls {
      margin-left: 68px; }

    .delete {
      margin-left: 0;
      @include clearfix;
      .controls {
        margin-left: 20px;
        float: right;
        label {
          display: block;
          padding-left: 24px; }
        input {
          width: 16px;
          height: 16px;
          padding: 0;
          margin: 0 4px 0 0;
          vertical-align: bottom;
          position: relative;
          top: 1px;
          *overflow: hidden; } } } } }


// overrides for special cases
fieldset {
  #div_id_remote_image {
    display: none; } }

form ul.advancedfileinput {
  width: 300px;
  float: left;
  li:first-child {
    margin-bottom: 10px; } }


form.form-horizontal {
  .controls {
    &.field-lookup {

      input, .field-lookup-holder {
        float: left; }

      .field-lookup-holder {
        float: right;
        margin: 0 0 0 30px;
        //background-color: #efefef
        background-color: #ffffff;
        border: 1px solid #efefef;

        span:empty {
          background: red;
          height: 120px; }

        max-width: 220px;

        display: none;
        padding: 0 4px 0 24px;

        max-height: 185px;
        overflow: auto;


        background-image: url(/static/img/icons/forms/compare.png);
        background-position: 4px center;
        background-repeat: no-repeat;

        &.hover {
          border: 1px solid #999;
          cursor: pointer; }


        // border: 1px solid #ccc
        color: #5a5a5a;

        &.lookup-match {
          //background-color: $primary-color-a-bg-light !important
          background-color: #ffffff !important; }

        &.lookup-diff {
          background-color: $warning-color-bg; } }

      // diff styling
      .field-lookup-holder {
        position: relative;
        span.changed {}

        span.diff {
          ins {
            background-color: #c6ffc6;
            text-decoration: none; }
          del {
            background-color: #ffc6c6; } } } } } }





