/* specific form styles
 * release edit view*/

// bulk apply form
div.bulk-edit-container {
  padding: 6px;
  background: $primary-color-b-bg-light;
  div.bulk_edit {

    p {
      margin-bottom: 6px; }

    input:focus,
    select:focus,
    textarea:focus {
      background: white;
      border-color: white; }

    .form-column {
      float: left;
      @include clearfix;
      &.main {
        width: 618px; }
      &.side {
        width: 200px; } }


    .btn {
      text-transform: none; } } }





#release_media_form {

  // hide error messages & hints
  .help-inline {
    // display: none
    font-size: 78%; }

  .form-header {
    background: #fafafa;
    margin: 7px 0 0 0;
    ul {
      margin: 0;
      padding: 0 10px;
      li {
        padding: 6px 0 0 0;
        color: #666666;
        text-transform: uppercase;
        font-size: 90%;
