    width: 823px; } }


// clearable file/image input
form.form-horizontal {
  .input-image, .input-file {
    .control-group {
      .control-label {
 }        // color: green
      .controls {
        ul {
          margin-left: 0;
          list-style-type: none;
          li input[type="checkbox"] {
            float: left;
            margin-right: 10px; } } } } } }



// inline notes, hints, info etc
form.form-horizontal {

  fieldset {

    div.notes-inline {
      color: $grayDark;
      padding: 5px 10px;
      margin-bottom: 10px;


      h1, h2, h3 {
        color: $grayDark;
        font-weight: bold;
        font-size: $baseFontSize;
        line-height: $baseLineHeight; }
      p {
        color: $grayDark;
        width: 80%; }

      // type dependent
      &.notes-info {
        color: $gray;
        padding: 0 0 0 2px;
        margin-bottom: 12px;
        p, h1, h2, h3 {
          color: $gray; } }



      // TODO: define what levels in use / style them in a generic way


      &.notes-warning {
        background-color: $warningBackground;
        color: $warningText;
        h1, h2, h3 {
          color: $warningText; }
        p {
          color: $warningText; } }

      &.notes-error {
        background-color: $errorBackground;
        color: $errorText;
        h1, h2, h3 {
          color: $errorText; }
        p {
          color: $errorText; } }

      &.notes-success {
        background-color: $successBackground;
        color: $successText;
        h1, h2, h3 {
          color: $successText; }
        p {
          color: $successText; } } } } }




