@charset "UTF-8";

/////////////////////////////////////////////////////////////////
// popup windows (search/merge/reassign/...)
/////////////////////////////////////////////////////////////////


// generic
.popup-actions {
  .loading-placeholder {
    text-align: center;
    text-transform: uppercase;
    padding: 53px 0 40px; }

  .btn.exit {
    color: white;
    background: transparent !important;
    border: none;
    margin-left: 10px;
    &:hover {
      color: #ccc; } }

  p.hint {
    color: white;
    padding-top: 8px; }



  .item.result, .item.object {
    margin: 0 0 10px 0;
    width: 97%;
    border-left: 4px solid transparent;
    img {
      width: 82px;
      padding: 4px 0 4px 4px;
      padding: 0 4px 0 4px; }

    .span10 {
      width: 80.982%; }

    cursor: pointer;
    &.hover {
      border-left: 4px solid white;
      background: #444; }
    &.selected {
      border-left: 4px solid $primary-color-a;
      background: #444; }

    span.warning {
      color: $errorText;
      text-transform: uppercase;
      font-weight: bold; } } }


/////////////////////////////////////////////////////////////////
// merge dialog
/////////////////////////////////////////////////////////////////
.popup-merge {

  width: 703px !important;
  max-width: 703px !important;
  padding: 10px 0 0 0;

  .info {
    border-bottom: 1px solid #5a5a5a;
    margin-bottom: 10px;

    strong {
      color: $white; }

    p {
      color: #999;
      margin-bottom: 4px; } }



  .item.object {

    img {
      width: 82px; }

    small.pk {
      padding-left: 10px;
      opacity: 0.7;
      font-weight: 300; }


    &.slave {
      opacity: 0.7; } }

  // content
  > div {
    section.title {
      h3 {
        color: #fff;
        small: {
          text-transform: none !important;
          font-size: 60%; } } }

    section.description {
      p {
        color: #efefef; }
      a {
        background: $primary-color-b;
        color: white; } }

    section.form {
      label {
        color: #efefef; } } }

  .action {
    input {
      color: white;
      width: 550px;
      border: none;
      background: #5a5a5a;
      padding: 4px 2px 4px 6px; } } }






