// form sidebar
.lookup.provider {

  &.listing {
    &.fixed {
      position: fixed;
      width: 260px;
      top: 120px; } }

  h2 {
    border-bottom: 1px solid #ccc; }

  .item {
    padding: 4px 4px 4px 4px;
    margin: 0px 0 0px 0;
    border-bottom: 1px solid #ccc;

    //span.name
    //font-size: 120%

    span.name {
      color: $primary-color-b;
      i {
        padding: 3px 0 3px 5px;
        width: 20px;
        display: block;
        float: left; } }
    // override actiuon background
    li > a {
      background: none; }

    &.available {
      // color: green
      cursor: pointer;
      &.hover {
        background-color: $primary-color-b-bg-light; } }

    &.unavailable {
      // color: green
      // cursor: not-allowed
      span.name {
        color: #a5a5a5;
        text-decoration: line-through; }
      &.hover {
        background-color: #efefef; } } }


  .lookup-settings {
    margin-top: 10px;
    .toggle-diff {
      input {
        float: left;
        display: block;
        width: 24px;
        margin-left: 4px; }
      label {
        color: $gray; } } } }












// special elements
div.daypart-grid {
  > ul {
    width: 40px;
    margin-right: 4px;
    float: left;
    > li {
      &.heading {
        background: transparent;
        color: #999;
        font-size: 9px; }
      &.daypart {

        background: #dcdcdc;
        border: 1px solid white;

        > a {
          display: block;
          height: 4px; }

        &:nth-child(2) {
          background-color: #FBD1FF;
          border-color: #FBD1FF; }
        &:nth-child(3) {
          background-color: #FFD8BF;
          border-color: #FFD8BF; }
        &:nth-child(4) {
          background-color: #FFF9BF;
          border-color: #FFF9BF; }
        &:nth-child(5) {
          background-color: #CCFFBF;
          border-color: #CCFFBF; }
        &:nth-child(6) {
          background-color: #BFFFEB;
          border-color: #BFFFEB; }
        &:nth-child(7) {
          background-color: #BFE9FF;
          border-color: #BFE9FF; }
        &:nth-child(8) {
          background-color: #BFC7FF;
          border-color: #BFC7FF; }

        &.selected {
          background-color: $primary-color-a !important;
          border: 1px solid $primary-color-a; } } } } }

