// Typography.scss
// Headings, body text, lists, code, and more for a versatile and durable typography system
// ----------------------------------------------------------------------------------------

// BODY TEXT
// ---------

p {
  margin: 0 0 $baseLineHeight / 2;
  font-family: $baseFontFamily;
  font-size: $baseFontSize;
  line-height: $baseLineHeight;
  small {
    font-size: $baseFontSize - 2;
    color: $grayLight; } }

.lead {
  margin-bottom: $baseLineHeight;
  font-size: 20px;
  font-weight: 200;
  line-height: $baseLineHeight * 1.5; }

// HEADINGS
// --------

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal;
  color: $grayDark;
  text-rendering: optimizelegibility;
  // Fix the character spacing for headings
  small {
    font-weight: normal;
    color: #aaaaaa; } }

h1 {
  font-size: 26px;
  line-height: 26px * 1.5;
  small {
    font-size: 16px; } }

h2 {
  font-size: 16px;
  line-height: 16px * 1.5;
  small {
    font-size: 12px; } }

h3 {
  font-size: 14px;
  line-height: 14px * 1.5;
  small {
    font-size: 12px; } }

h4 {
  font-size: 12px;
  line-height: 12px * 1.5; }

h5 {
  font-size: 12px;
  line-height: 12px * 1.5; }

h6 {
  font-size: 12px;
  line-height: 12px * 1.5; }






