// Page header
.page-header {
  // padding-bottom: $baseLineHeight - 1
  // margin: $baseLineHeight 0
  // border-bottom: 1px solid $grayLighter
  padding-top: $basePadding / 3;
  &.padded {
    padding: $basePadding; }
  &.padded-h {
    padding-left: $basePadding;
    padding-right: $basePadding; }
  &.padded-v {
    padding-top: $basePadding;
    padding-bottom: $basePadding; } }

.page-header h1 {
  line-height: 1; }

// Page content (basically markdown content)
.page-content {
  // padding-bottom: $baseLineHeight - 1
  // margin: $baseLineHeight 0
  // border-bottom: 1px solid $grayLighter
  padding-top: $basePadding / 3;
  &.padded {
    padding: $basePadding; }
  &.padded-h {
    padding-left: $basePadding;
    padding-right: $basePadding; }
  &.padded-v {
    padding-top: $basePadding;
    padding-bottom: $basePadding; } }

// LISTS
// -----

// Unordered and Ordered lists
ul, ol {
  padding: 0;
  margin: 0 0 $baseLineHeight / 2 25px; }

ul ul,
ul ol,
ol ol,
ol ul {
  margin-bottom: 0; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li {
  line-height: $baseLineHeight; }

ul.unstyled,
ol.unstyled {
  margin-left: 0;
  list-style: none; }

// Description Lists
dl {
  margin-bottom: $baseLineHeight; }

dt,
dd {
  line-height: $baseLineHeight; }

dt {
  font-weight: normal;
  opacity: 0.6;
  text-align: left; }

dd {
  margin-left: $baseLineHeight / 2; }

// MISC
// ----

// Horizontal rules
hr {
  margin: $baseLineHeight 0;
  border: 0;
  border-top: 1px solid $hrBorder;
  border-bottom: 1px solid $white; }

// Emphasis
strong {
  font-weight: bold; }

em {
  font-style: italic; }

.muted {
  color: $grayLight; }

// Abbreviations and acronyms
abbr {
  font-size: 90%;
  text-transform: uppercase;
  border-bottom: 1px dotted #ddd;
  cursor: help; }

// Blockquotes
blockquote {
  padding: 0 0 0 15px;
  margin: 0 0 $baseLineHeight;
  border-left: 5px solid $grayLighter;
  p {
    margin-bottom: 0;
    @include font-shorthand(16px, 300, $baseLineHeight * 1.25); }
  small {
    display: block;
    line-height: $baseLineHeight;
    color: $grayLight;
    &:before {
      content: '\2014 \00A0'; } }
  // Float right with text-align: right
  &.pull-right {
    float: right;
    padding-left: 0;
    padding-right: 15px;
    border-left: 0;
    border-right: 5px solid $grayLighter;
    p,
    small {
      text-align: right; } } }

// Quotes

q:before,
q:after,
blockquote:before,
blockquote:after {
  content: ""; }

// Addresses
address {
  display: block;
  margin-bottom: $baseLineHeight;
  line-height: $baseLineHeight;
  font-style: normal; }

// Misc
small {
  font-size: 100%; }

cite {
  font-style: normal; }
