$popup-height: 360px;
$popup-width: 480px;

.select-playlist {

  height: $popup-height + 8 !important;

  // TODO: make generic
  ::-webkit-input-placeholder {
    color: #a5a5a5; }
  ::-moz-placeholder {
    color: #fff; }

  // content
  > div {
    section.title {
      display: none; // f.t.m.
      height: 30px;
      h3 {
        color: #fff;
        small: {
          text-transform: none !important;
          font-size: 60%; } }

      a {
        color: #fff; } }

    section.search {
      height: 30px;
      input {
        padding-left: 4px;
        margin-bottom: 0;
        width: 100px;
        float: left;
        width: $popup-width - 28px;
        //height: 10px
        border-color: #333;
        background: transparent; }

      &:hover {
        input {
          background: #424242;
          border-color: #999;
          color: white; } } }


    section.listing {
      height: $popup-height - 70px;
      p.notice {
        color: white;
        text-align: center;
        padding-top: 20px; }

      p.load-more {
        color: white;
        text-align: center;
        padding-top: 20px;
        .btn {
          width: 100%;
          background: rgb(66, 64, 64);
          color: #BFBFBF;
          border-color: rgb(66, 64, 64);
          border-width: 1px;
          &:hover {
            color: #fff;
            border-color: #fff;
            background: #000; } } }

      .pane {
        background: #444; }

      .slider {
        background: #999; }

      // the actual playlist
      .item {
        border-top: 1px solid #424242;
        cursor: pointer !important;
        .image img {
          height: 31px;
          width: 31px; }
        .information {
          ul {
            margin: 0;
            li {
              line-height: 15px;

              a {
                color: white; }

              span {
                font-size: 11px;
                &.number {
                  font-size: 9px; }
                &.collected {
                  padding-right: 10px; } }

              small {
                font-size: 10px;
                padding-right: 10px; } } } }

        &.match {
          background-color: #484848; }

        &.hover {
          background-color: $primary-color-a-bg-light;
          li *, li > strong > a {
            color: #333; } }

        &.loading {
          opacity: 0.7;
          cursor: wait !important; }

        // guess that obsolete (generic)
        &.hidden {
          opacity: 0.8; } } }

    section.form {
      height: 30px;
      .btn-toolbar {

        margin-bottom: 0;
        .btn-group {
          width: 100%; }

        input {

          padding-left: 4px;
          margin-bottom: 0;
          float: left;
          width: 372px;
          //height: 10px
          border-color: #333;
          background: transparent; }

        .btn {
          opacity: 0; } }

      &:hover {
        .btn-toolbar {
          .btn {
            opacity: 1; }
          input {
            background: #424242;
            border-color: #999;
            color: white; } } }

      label {
        color: #efefef; } }

    section.form {
      fieldset.color {
        .controls {
          height: 40px;
          label {
            height: 24px;
            width: 14px;
            float: left;
            padding-top: 0;
            border: 4px solid #333;
            &.selected {
              border-color: $primary-color-b; } } } } } } }


$popup-window-width: 400px;
$popup-window-height: 800px;
$popup-content-width: 378px;
.select-playlist-popup {
  @extend .select-playlist;
  height: 100% !important;
  width: 100% !important;
  max-width: 100% !important;
  border: none !important;
  > div {
    section.title {
      display: block; }
    section.search {
      height: 30px;
      input {
        padding-left: 4px;
        margin-bottom: 0;
        float: left;
        width: $popup-content-width; } }

    section.listing {
      height: $popup-window-height - 120px; }

    section.form {
      .btn-toolbar {
        input {
          width: $popup-content-width - 100px; } } } } }
